body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.playgroundCode {
  display: none;
}

body {
     min-width:0;
     min-width:initial; 
     max-width: none; 
     max-width: initial; 
}

.myButton {
  margin: 10px;
  padding: 10px;
  background: #45a5ff;
  color: white;
  font-weight: 600;
  cursor: pointer;
}


.unconfigured {
  padding: 24px;
  border: 2px #d81e1e solid;
  color: #d81e1e;
  font-size: 16px;
  font-weight: 800;
}
